 .App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
} 
.main {
  margin-top: 5em;
  margin-left: 1em;
  margin-right: 1em;
  
}
.contactList{
  text-align: center;
}

.center {
  justify-content: center;
  padding: 10px;
}

.ui.search input {
  width: 100%;
  border-radius: 0 !important;
}

.item {
  padding: 15px 0px !important;
  margin-left: 1em;
  margin-right: 1em;
  
}

i.icon {
  float: right;
  font-size: 20px;
  cursor: pointer;
}

.imageContactDetail{
  background-image: url("/src/images/user4.png");
  background-repeat: no-repeat;
  margin-top: 1em;
  height: 290px;
  width: 290px;
  
}
